			params: {
				class_id: '', //课程id
				name: '', //姓名
				mobile: '' //手机号
			}
		}
	},
	onLoad(options) {
		this.params.class_id = options.class_id
	},
	methods: {
		courseBooking() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.reservation, this.params).then((res) => {
